}
.container_smile .radio {
    position: relative;
    background-color: white;
    background-color: white;
    /* OPACITY AND COLOR */
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    border-radius: 3px;
    border: 1px solid #d9e1e6;
